* {
    font-size: 16px;
    line-height: 19px;
}

body {
    background: #fafafa;
    color: #212C4C;
    padding: 0 8em;
    margin: 0;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

@media screen and (max-width: 1024px) {
    body {
        padding: 0 1em;
    }
}

body,
input,
textarea {
    font-family: 'Karla', sans-serif;
}

body > form {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    width: 100%;
    min-width: 375px;
    max-width: 1660px;
    margin: 0 auto;
}

.surroundcontent {
    flex: 1 0 auto;
}

.hiddencontrol {
    width: 1px;
    height: 1px;
    position: absolute;
    left: -9999px;
}

/* Standardises the placeholder colour - these rules cannot be combined */
::-moz-placeholder {
    color: #CCCCCC;
    opacity: 1;
}
:-ms-input-placeholder {
    color: #CCCCCC;
    opacity: 1;
}
::placeholder {
    color: #CCCCCC;
    opacity: 1;
}

:-ms-input-placeholder {
    color: #CCCCCC;
}

::-ms-input-placeholder {
    color: #CCCCCC;
}
